/* App.css */
/* App.css */

/* Reset and base */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f7fa; /* light soft background */
  color: #333333; /* dark text for good contrast */
  line-height: 1.6;
}

.App {
  text-align: center;
  padding: 40px 20px;
  max-width: 800px;
  margin: 0 auto; /* center horizontally */
}

h1 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 30px;
  font-weight: 700;
}

/* Links styling */
a {
  color: #3498db;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Utility: Center content vertically and horizontally if needed */
.centered-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

/* Add smooth fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
